.container {
  width: 1160px;
  margin: 0 auto;
}

.container-sm {
  max-width: 715px;
  margin: 0 auto;
}

.block-title {
  font-size: 48px;
  color: white;
  line-height: 1.2;
  font-family: 'HelveticaNeue-Bold';
}

.header {
  background-size: cover;
  padding: 85px 0px 26px 0px;
  background-repeat: no-repeat;
  position: relative;
}

.header__content {
  max-width: 530px;
}

.header__title {
  margin-bottom: 272px;
}

.header-details__description {
  color: white;
  font-size: 18px;
  line-height: 1.2;
  letter-spacing: 0.6px;
  margin-bottom: 8px;
  font-family: 'MyriadPro-Regular';
}

.header-details__data {
  font-size: 12px;
  color: white;
  font-family: 'MyriadPro-Regular';
  margin-right: 35px;
}

.header__social {
  background-color: #af1b15;
  position: absolute;
  right: 0px;
  bottom: 27px;
}

.social__link {
  text-decoration: none;
  padding: 14px;
  display: inline-block;
}

.social__link svg {
  fill: #fff;
  width: 15px;
  height: 15px;
}

.short-text {
  padding-top: 63px;
}

.short-text p {
  font-size: 20px;
  color: #383838;
  line-height: 1.3;
  font-family: 'PTSerif-Regular';
  margin-bottom: 25px;
}

.facts {
  padding-top: 58px;
}

.facts__inner {
  padding: 0px 97px;
}

.facts__title {
  color: #383838;
  margin-bottom: 67px;
}

.facts-item {
  margin-bottom: 73px;
}

.facts-item__title {
  font-family: 'HelveticaNeue-Regular';
  font-size: 30px;
  color: #383838;
  line-height: 1.2;
  border-bottom: 2px solid #af1b15;
  margin-right: 47px;
  margin-bottom: 40px;
  position: relative;
}

.facts-item__title span {
  font-size: 48px;
  color: #af1b15;
  font-family: 'PTSerif-Italic';
  margin-right: 9px;
}

.facts-item__number {
  font-family: 'HelveticaNeue-Bold';
  font-size: 155px;
  color: rgba(56, 56, 56, 0.102);
  line-height: 1em;
  position: absolute;
  top: -45px;
  left: -23px;
}

.facts-item__text p {
  font-size: 20px;
  color: #383838;
  line-height: 1.3;
  font-family: 'PTSerif-Regular';
  margin-bottom: 23px;
  text-indent: initial;
}

.facts-item__text span {
  font-family: 'PTSerif-Italic';
  font-size: 20px;
  text-indent: 18px;
  display: block;
}

.footer {
  background-color: #af1b15;
  padding: 52px 0px 27px 0px;
}

.footer__video {
  margin-bottom: 124px;
}

.footer__logo {
  text-align: center;
}
